<template>
  <div class="playerList">
    <b-col
      center
      class="startGameBox mt-4"
      v-if="playersCount > 1 && isIamAdmin && !gameStarted"
    >
      <b-button @click="startGame()" variant="primary">
        {{ isFirstGame ? "Start Game" : "Play Again" }}
      </b-button>
    </b-col>
    <b-container :class="['reltv', gameStarted ? 'my-5' : 'my-3']">
      <!-- <div
                class="currentPlayerLabel"
                :style="{ left: 'calc(' + (currentPlayerLabelLeftValue + '% - 10px)') }"
                v-if="gameStarted"
            ></div> -->
      <b-row class="playersListWrapper">
        <b-col v-for="(player, playerId) in myRoom" :key="playerId">
          <div
            :class="[
              'playerLabel',
              'reltv',
              isPlayerOnline(playerId) ? '' : 'offline',
              player.winCount ? 'hasWinCount' : '',
              myRoomAdmin == playerId ? 'admin' : ''
            ]"
          >
            <div v-if="myRoomAdmin == playerId" class="adminLabel">Admin</div>
            <CrownSvg
              v-if="highestScorePlayerIds.indexOf(playerId) >= 0"
              fill="#fff"
              class="mr-1"
            />
            <strong>{{ player.playerName }}</strong>
            <div v-if="player.winCount" class="noOfWins">
              Wins: {{ player.winCount }}
            </div>
            <div
              class="timeOutBar"
              :style="{
                width:
                  (playerId == CurrentPlayerId && gameStarted
                    ? timeoutBarPercentage
                    : 0) + '%'
              }"
            ></div>
          </div>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>
<script>
import store from "@/store";
import Server from "@/server";
import CrownSvg from "@/components/svg/Crown";
export default {
  methods: {
    startGame: () => {
      Server.startGame();
    },
    isPlayerOnline: playerId => {
      return store.state.room.myRoom[playerId].isOnline;
    }
  },
  computed: {
    isIamAdmin: () => store.getters.isIamAdmin,
    highestScorePlayerIds: () => store.getters.highestScorePlayerIds,
    isFirstGame: () => store.getters.isFirstGame,
    gameStarted: () => store.state.room.gameStarted,
    playersCount: () => store.getters.playersCount,
    myRoom: () => store.state.room.myRoom,
    myRoomAdmin: () => store.state.room.myRoomAdmin,
    CurrentPlayerId: () => store.state.room.myRoomCurrentPlayer,
    myId: () => store.state.player.id,
    totalPlayers: () => store.getters.totalPlayers,
    currentPlayerIndex: () => store.getters.currentPlayerIndex,
    timeoutBarPercentage: () => store.getters.timeoutBarPercentage,
    currentPlayerLabelLeftValue: function() {
      var { totalPlayers, currentPlayerIndex } = this;
      return (currentPlayerIndex / totalPlayers) * 100 - 100 / totalPlayers / 2;
    }
  },
  components: { CrownSvg }
};
</script>
