import browser from "./browser";
import localStorage from "./localStorage";

function createStatusText(winnerNames) {
  if (!winnerNames.length) return '';
  else if (winnerNames.length == 1) return winnerNames[0];
  let lastName = winnerNames[winnerNames.length - 1];
  winnerNames.pop();
  return winnerNames.join(', ') + ' and ' + lastName;
}

export default {
  browser,
  localStorage,
  createStatusText
};
